<template>
  <header :class="{ scrolled: hasScrolled }">
    <div class="menu">
      <img :src="logo" alt="logo Cedray" />
      <span><a href="/servicos">SERVIÇOS</a></span>
      <span><a href="/comercios">COMÉRCIO</a></span>
    </div>
    <div class="contact-info">
      <a href="https://www.instagram.com/cedraydrywall_comercio" target="_blank"><i class="bi bi-instagram"></i></a>
      <a href="https://wa.me/551125488847" target="_blank"><i class="bi bi-whatsapp"></i></a>
      <span>(11) 2548-8847 / (11) 2548-8873</span>
      <div :class="['cart', { 'shake': isShaking }]" v-if="this.$route.path === '/comercios'"
        @click="this.$emit('toggleVisibility')">
        <i class="bi bi-cart-fill">
          Carrinho</i>
        <span class="qtdCartItems">{{ qtdItems }}</span>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "HeaderComponent",
  props: {
    qtdItems: {
      type: Number
    }
  },
  data() {
    return {
      hasScrolled: false,
      logo: this.$route.path === '/servicos' ? 'logo-servicos.png' : 'logo-comercio.png',
      isShaking: false
    };
  },
  watch: {
    qtdItems() {
      this.isShaking = true;
      setTimeout(() => {
        this.isShaking = false;
      }, 500);
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.hasScrolled = window.scrollY > 0;
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}

header {
  display: flex;
  align-items: center;
  position: fixed;
  background-color: transparent;
  width: 100%;
  z-index: 998;
  padding: 15px 0px;
  transition: background 0.3s;

  color: white;
}

header .menu a {
  color: white;
}

header.scrolled {
  background-color: white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.267);
  color: black;
}

header.scrolled .menu a {
  color: black;
}


header .menu {
  display: flex;
  align-items: center;
  font-weight: 500;
  padding: 0px 100px;
}

header img {
  width: 70px;
  border-radius: 50px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.267);
  margin-right: 45px;
}

header .menu span a {
  padding: 5px;
}

header .menu span:hover {
  background-color: #86c226;
  border-radius: 5px;
  cursor: pointer;
}

header .contact-info {
  display: flex;
  align-items: center;
  gap: 3px;
  margin-left: auto;
  margin-right: 100px;

}

header .contact-info i {
  font-size: 1.4em;
}

header .contact-info .bi-facebook {
  color: #3a559f;
  margin-right: 15px;
}

header .contact-info .bi-whatsapp {
  color: green;
}
header .contact-info .bi-instagram {
  color: #ed3131;
}

header .contact-info span {
  font-weight: 600;
}

.cart {
  color: white;
  border: 1px solid #86c226;
  background-color: #86c226;
  padding: 2px;
  border-radius: 5px;
  margin-left: 5px;
  padding: 0px 50px;
  cursor: pointer;
  transition: background 0.2s;
  position: relative;
}

.cart:hover {
  background-color: white;
  color: #86c226;
}

.cart:hover i {
  color: #86c226;
}

.cart i {
  font-size: 1em !important;
}

.shake {
  animation: shake 0.5s;
}

.qtdCartItems {
  position: absolute;
  top: -12px;
  background-color: white;
  color: black;
  border-radius: 50%;
  padding: 0 6px;
  border: 1px solid rgba(0, 0, 0, 0.555);
}


@keyframes shake {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-5px);
  }

  50% {
    transform: translateX(5px);
  }

  75% {
    transform: translateX(-5px);
  }

  100% {
    transform: translateX(0);
  }
}

@media screen and (max-width: 1170px) {
  header {
    display: none;
  }
}
</style>