<template>
  <section>
    <i class="bi bi-x-circle" style="position: absolute; left: 15px; top: 15px; font-size: 1.5em"
      @click="this.$emit('toggleVisibility')"></i>
    <h1>Carrinho</h1>
    <hr />
    <p class="sub-title">{{ productMessage }}</p>
    <div style="overflow-y: scroll; max-height: 50vh">
      <div v-for="(product, index) of cart" :key="index" class="product">
        <div>
          <i class="bi bi-x-square-fill" style="color: rgb(197, 15, 15); margin-right: 5px"
            @click="removeProduct(product.name)"></i>
          <span>{{ product.name }}</span>
        </div>
        <div class="variations" v-if="product.selectedVariations.length !== 0">
          <span>Variações selecionadas</span>
          <hr>
          <div v-for="(variation, index) of product.selectedVariations" :key="index">
            <span :for="variation">{{ variation.name }}</span><br>
            <span :for="variation"> Quantidade: {{ variation.qtd }}</span>
            <hr>
          </div>
        </div>
      </div>
    </div>
    <button v-if="this.cart.length" @click="sendProducts" class="orcamento">
      Fazer orçamento
    </button>
  </section>
</template>

<script>
export default {
  name: "CartListComponent",
  props: {
    products: {
      type: Array,
    },
  },
  data() {
    return {
      cart: this.products,
    };
  },
  computed: {
    productMessage() {
      return this.cart.length
        ? "Produtos"
        : "Nenhum produto adicionado ao carrinho";
    },
  },
  methods: {
    sendProducts() {
      const cart = this.cart;
      const myProducts = cart.map((product) => {
        let productString = `%0A${product.name}`;

        if (product.selectedVariations.length !== 0) {
          productString += ` %0A - Variações: %0A${product.selectedVariations.map((el) =>
          `   - ${el.name} - Qtd: ${el.qtd} %0A`
          ).join('')}`;
        }

        return productString;
      });
      const msg = `Olá, gostaria de solicitar um orçamento! Tenho interesse nos seguintes produtos:%0A
${myProducts}
      `;
      window.open(`https://wa.me/551125488847?text=${msg}`, "_blank");
    },
    removeProduct(product) {
      const cart = this.cart;
      const itemIndex = cart.find((item) => item.name === product);
      const index = cart.indexOf(itemIndex);
      if (index > -1) {
        cart.splice(index, 1);
      }
    },
  },
};
</script>

<style scoped>
section {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: white;
  width: 350px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.377);
  z-index: 999;
  text-align: center;
}

h1 {
  color: #86c226;
  text-align: center;
  font-size: 1.8em;
  font-weight: 600;
  padding: 20px 0;
}

hr {
  opacity: 30%;
}

.sub-title {
  text-align: center;
  color: #86c226;
  font-weight: 600;
  font-size: 1.2em;
}

.product {
  border-bottom: 2px solid #86c226;
  font-weight: 600;
  padding: 15px 0;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.product .variations {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 10px 0;
  height: 100px;
  overflow-y: scroll;
  width: 100%;
  margin: 15px 0;
}

.variations::-webkit-scrollbar {
  width: 10px;
}

.variations::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.variations::-webkit-scrollbar-thumb {
  background: #888;
}

.orcamento {
  background-color: #86c226;
  border-radius: 10px;
  border: 1px solid #86c226;
  color: white;
  padding: 3px 35px;
  font-weight: 600;
  margin: 15px 0;
  cursor: pointer;
  transition: background 0.2s;
  font-size: 1em;
}

.orcamento:hover {
  background-color: white;
  color: #86c226;
}
</style>