<template>
  <HeaderComponent @toggleVisibility="this.showCartList = true" :qtdItems="qtdCartItems" />
  <MobileHeaderComponent :qtdItems="qtdCartItems" @openCart="this.showCartList = true"
    @openMenu="this.mobileMenu = true" />
  <MobileMenuComponent v-if="mobileMenu" @toggleVisibility="this.mobileMenu = false" />
  <BannerComercioComponent />
  <h1 class="sub-title">PRODUTOS</h1>
  <p style="text-align: center; margin: auto 15px">
    Adicione produtos ao seu carrinho e solicite um orçamento via WhatsApp com
    apenas alguns cliques <br />
    <b class="cart-message">Para visualizar seu carrinho, clique no botão no canto superior
      direito</b>
  </p>
  <section class="products">
    <div v-for="(product, index) of products" :key="index" class="single-product">
      <img :src="`produtos/${product.image}`" :alt="product.name" class="product-image" />
      <div class="product-name">
        <p>{{ product.name }}</p>
      </div>
      <div class="show-details" @click="showDetails(product)">
        <span>Visualizar</span>
      </div>
    </div>
  </section>
  <dialog ref="modal">
    <div class="product-details">
      <div class="product-div">
        <img :src="`produtos/${details.image}`" :alt="details.name" class="image-details">
        <p>{{ details.name }}</p>
        <div class="add-cart" @click="addToCart(details)" v-if="!details.variations">
          <i class="bi bi-cart-check-fill"></i>
          <span>Adicionar ao carrinho</span>
        </div>
      </div>
      <i class="bi bi-x-circle" style="position: absolute; right: 15px; top: 15px; font-size: 1.5em"
        @click="this.$refs.modal.close()"></i>
      <div class="variations-div" v-if="details.variations">
        <p> Variações </p>
        <hr style="width: 100%;">
        <div class="variations-list">
          <div v-for="(variation, index) of details.variations" :key="index" class="variation">
            <div class="checkbox-div">
              <label :for="variation">{{ variation }}</label>
              <input type="checkbox" :value="variation"
                @change="handleCheckboxChange(variation, details.name, $event)" />
            </div>
            <div>
              <label :for="variation">Qtd. </label>
              <input type="text" @change="updateQtd(variation, $event)" class="qtd" />
            </div>
          </div>
        </div>
        <div class="add-cart" @click="addToCart(details)">
          <i class="bi bi-cart-check-fill"></i>
          <span>Adicionar ao carrinho</span>
        </div>
      </div>
    </div>
  </dialog>
  <CartListComponent :products="this.cart" @toggleVisibility="this.showCartList = false" v-if="showCartList" />
  <i :class="QuickNavigationIcon" @click="QuickNavigate"></i>
  <FooterComponent />
</template>
<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
import BannerComercioComponent from "@/components/BannerComercioComponent.vue";
import CartListComponent from "@/components/CartListComponent.vue";
import FooterComponent from "@/components/FooterComponent.vue";
import MobileMenuComponent from "@/components/MobileMenuComponent.vue";
import MobileHeaderComponent from "@/components/MobileHeaderComponent.vue";

export default {
  name: "ComerciosView",
  components: {
    HeaderComponent,
    BannerComercioComponent,
    CartListComponent,
    FooterComponent,
    MobileMenuComponent,
    MobileHeaderComponent,
  },
  data() {
    return {
      cart: [],
      showCartList: null,
      products: [],
      hasScrolled: false,
      mobileMenu: false,
      QuickNavigationIcon: "bi bi-arrow-down-square-fill",
      details: [],
    };
  },
  computed: {
    qtdCartItems() {
      return this.cart.length;
    },
  },
  mounted() {
    this.listProducts();
  },
  methods: {
    showDetails(product) {
      this.$refs.modal.showModal()
      this.details = { ...product }
      this.details.selectedVariations = []
    },
    addToCart(cart_product) {
      const cart = this.cart;
      const existedProduct = cart.find((product) => product.name === cart_product.name)

      if (!existedProduct) {
        this.cart.push(cart_product);
        this.details = []
        this.$refs.modal.close()
        return
      }

      cart_product.selectedVariations.forEach(newVariation => {
        const existedVariation = existedProduct.selectedVariations.find(
          variation => variation.name === newVariation.name
        );

        if (existedVariation) {
          existedVariation.qtd = newVariation.qtd;
        } else {
          existedProduct.selectedVariations.push(newVariation);
        }
      })
      this.details = []
      this.$refs.modal.close()
    },
    async listProducts() {
      try {
        const response = await fetch("./produtos/produtos.json");
        if (!response.ok) {
          throw new Error("Falha ao carregar o arquivo JSON");
        }
        const data = await response.json();
        this.products = data;
      } catch (error) {
        console.error(error);
      }
    },
    handleScroll() {
      this.hasScrolled = window.scrollY > 0;
    },
    QuickNavigate() {
      if (this.QuickNavigationIcon === "bi bi-arrow-down-square-fill") {
        window.scrollTo({
          top: document.body.scrollHeight,
          behavior: "smooth",
        });
        this.QuickNavigationIcon = "bi bi-arrow-up-square-fill";
      } else {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
        this.QuickNavigationIcon = "bi bi-arrow-down-square-fill";
      }
    },
    handleCheckboxChange(option, prod, event) {
      if (event.target.checked) {
        const details = this.details;
        details.selectedVariations.push({ name: option, qtd: null })
      }
    },
    updateQtd(product, event) {
      const details = this.details;
      const itemIndex = details.selectedVariations.find((item) => item.name === product);
      itemIndex.qtd = Number(event.target.value);
    },
  },
};
</script>
<style scoped>
.sub-title {
  color: #86c226;
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sub-title::before,
.sub-title::after {
  content: "";
  display: block;
  height: 0.09em;
  min-width: 30vw;
}

.sub-title::before {
  background: linear-gradient(to right, rgba(240, 240, 240, 0), #86c226);
  margin-right: 4vh;
}

.sub-title::after {
  background: linear-gradient(to left, rgba(240, 240, 240, 0), #86c226);
  margin-left: 4vh;
}

.products {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 100px;
  gap: 100px;
}

.single-product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 10px 0;
  width: 250px;
  min-width: 250px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.356);
}

.single-product .product-name {
  background-color: #86c226;
  color: white;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.9em;
}

.product-name p {
  padding: 0 5px;
  height: fit-content
}

.single-product .product-image {
  width: 200px;
  height: 150px;
  margin-bottom: 15px;
}

.show-details {
  border: 1px solid #86c226;
  padding: 10px 8px;
  border-radius: 10px;
  text-transform: uppercase;
  font-weight: 600;
  color: #3f423b;
  font-size: 1em;
  transition: background 0.5s;
  cursor: pointer;
  margin-top: 10px;
}

.show-details:hover {
  background-color: #86c226;
  color: white;
}

.add-cart {
  display: flex;
  align-items: center;
  justify-content: center;

  border: 1px solid #86c226;
  padding: 2px 8px;
  border-radius: 10px;
  text-transform: uppercase;
  font-weight: 600;
  color: #3f423b;
  font-size: 0.8em;
  gap: 7px;
  transition: background 0.5s;
  cursor: pointer;
  margin: 30px 15px;
}

.add-cart:hover {
  background-color: #86c226;
  color: white;
}

.add-cart:hover i {
  color: white;
}

.add-cart i {
  color: #86c226;
  font-size: 2em;
}

.bi-arrow-down-square-fill,
.bi-arrow-up-square-fill {
  position: fixed;
  color: #86c226;
  right: 50px;
  bottom: 50px;
  font-size: 3em;
}

dialog {
  border: 1px solid rgba(226, 218, 218, 0.898);
  border-radius: 15px;
  padding: 0
}

dialog:focus {
  outline: none
}

.product-details {
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 0;
  padding-right: 0;
  overflow: hidden;
}

dialog .product-div {
  text-align: center;
  border-right: 1px solid #86c226;
}

dialog .product-div p {
  background-color: #86c226;
  color: white;
  padding: 10px;
  font-weight: 600;
}

dialog .variations-div {
  width: 350px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

dialog .variations-div p {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.2em;
  margin: 0;
}

.variations-list {
  max-height: 150px;
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: left;
  font-size: 0.8em;
}

.variations-list .variation {
  display: flex;
  justify-content: space-between;
  margin: 5px auto;
}

.variations-list .variation label {
  font-weight: 600;
}

.variations-list .variation .qtd {
  width: 30px;
  border-radius: 5px;
  border: 2px solid rgb(191, 185, 185)
}

.variations-list .variation .checkbox-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
}

dialog img {
  width: 50%;
}


@media screen and (max-width: 1170px) {
  .cart-message {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .product-details {
    flex-direction: column;
    height: initial;
  }
}
</style>