<template>
  <section>
    <div
      :class="['cart', { shake: isShaking }]"
      v-if="this.$route.path === '/comercios'"
      @click="this.$emit('openCart')"
    >
      <i class="bi bi-cart-fill"> Carrinho</i>
      <span class="qtdCartItems">{{ qtdItems }}</span>
    </div>
    <i class="bi bi-list" @click="this.$emit('openMenu')"></i>
  </section>
</template>

<script>
export default {
  name: "MobileHeaderComponent",
  props: {
    qtdItems: {
      type: Number,
    },
  },
  data() {
    return {
      isShaking: false
    };
  },
  watch: {
    qtdItems() {
      this.isShaking = true;
      setTimeout(() => {
        this.isShaking = false;
      }, 500);
    },
  },
};
</script>

<style scoped>
section {
  display: none;
}
@media (max-width: 1170px) {
  section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 15px;

    position: fixed;
    width: 100%;
    background-color: #86c226;
    box-shadow: 0 2px 7px 0 black;
  }

  section .cart {
    color: white;
    border: 1px solid #ffffff;
    border-radius: 5px;
    padding: 0px 50px;
    height: fit-content;
  }
  section .cart i {
    font-size: 1em !important;
  }

  section .bi-list {
    font-size: 1.5em;
    color: black;
    background-color: white;
    padding: 0px 8px;
    border-radius: 5px;
    margin-right: 25px;
  }

  .qtdCartItems {
    position: absolute;
    top: 25px;
    background-color: white;
    color: black;
    border-radius: 50%;
    padding: 0 7px;
    border: 1px solid rgba(0, 0, 0, 0.555);
    font-size: 0.9em;
    margin-left: 5px;
  }
}
</style>